<template>
    <div class="popup-info">
        <div>
            <img src="@/assets/img/icon-ok-big.svg" alt="">
        </div>

        <div class="text mt32">
            <div class="text-size-xxl text-color-green text-bolder">Ваша заявка принята!</div>

            <p class="mt24">
                В начале её рассмотрят модераторы, затем – руководитель проекта.<br>
                Обычно это занимает от 1 до 3-х дней. Мы будем информировать вас<br>
                о ходе рассмотрения по электронной почте и СМС.
            </p>
        </div>

        <div class="mt32">
            <button :class="{'btn--disabled': disableButton, 'btn--white-green': !disableButton}" class="btn" @click="closeCooperationAccepted()">Степенно жду!</button>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import Popup from '@/components/modal/Popup.vue'

    export default {
        name: "cooperationAccepted",
        components: {
            Popup
        },
        data(){
            return {
                disableButton: true,
            }
        },
        mounted(){
            this.disableButton = true;
            setTimeout(()=>{
                this.disableButton = false
            },3000)
        },
        methods: {
            closeCooperationAccepted: function () {
                if(!this.disableButton){
                    this.$store.commit('popups/close', {name: this.popups.cooperationAccepted.name});
                    window.scroll(0,0);
                    this.$router.push('/');
                }
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        }
    }
</script>

