                        <span class="text">Условия сотрудничества</span>
                    </a>
                </div>
            </div>



            <div class="cooperation__text">
                На одну компанию допускается только одна заявка. Если у вас несколько направлений, выберите одно, самое интересное.<br>
                После одобрения вы получите личный кабинет, где сможете создавать скидки по всем направлениям.
            </div>



            <div class="cooperation__form">
                <div class="row-b" id="cooperation-company">
                    <div class="row-b__col elem-pos-fit">
