                                      :placeholder-input="'Например, «Сатурн»'"
                                      :placeholder="'Название компании'"></input-styled>
                    </div>
                </div>


                <div class="row-b"  id="cooperation-city">
                    <div class="row-b__col elem-pos-fix" style="width: 320px; ">
                        <a href="javascript:void(0);" class="cooperation-link-city" :class="{active: cityFocus}">
                            <span class="region">Регион</span>
                            <span class="t" @click="cityFocus = !cityFocus">{{ primaryForm.city.valueText ? primaryForm.city.valueText : currentCityIp.name }}</span>
                            <city-select :is-set="false"
                                         @set:city="setCity"
                                         :prop-current="primaryForm.city.value ? primaryForm.city.value : currentCityIp"
                                         v-show="cityFocus" ></city-select>
                        </a>
                    </div>

                    <div class="row-b__col elem-pos-fit" :class="{'--active':primaryForm.city.focus}">
                        <div class="text-size-s construct-form__note">
