                            укажите один, самый основной
                        </div>
                    </div>
                    <!--
                    <popup :show="popups.citySelectReturn.show"
                           :width="456"
                           :add-class="'popup-city'"
                           :name="popups.citySelectReturn.name">
                        <city-select :text-note="'(Если города нет в списке – значит, мы в нём не работаем...)'"
                                     @set:city="setCity"
                                     :is-set="false"
                                     :prop-current="primaryForm.city.value ? primaryForm.city.value : currentCityIp"
                                     :is-show-all="true">
                        </city-select>
                    </popup>
                  -->
                </div>


                <div class="row-b"  id="cooperation-direction">
                    <div class="row-b__col elem-pos-fit">
                        <input-styled
                                :disabled="currentStep > 4"
                                :isValidate="currentStep > 4"
                                name="direction"
                                v-model="primaryForm.direction.value"
                                :placeholder="'Основная деятельность'"
                                :placeholder-input="'Например, «Штукатурные выравнивающие смеси»'"
                                :is-error="primaryForm.direction.error"
                                :error-text="primaryForm.direction.errorText"
                                :max-length="true"
                                :max-length-num="50"
                                @focus="removeError(primaryForm.direction)"
                                @blur="blur(primaryForm.direction)"
                        ></input-styled>
                    </div>
                </div>


                <div class="row-b"  id="cooperation-average">
                    <div class="row-b__col elem-pos-fix" style="width: 320px; ">
                        <input-price v-model="primaryForm.average.value"
                                     :disabled="currentStep > 4"
                                     :isValidate="currentStep > 4"
                                     name="average"
                                      :is-error="primaryForm.average.error"
                                      :error-text="primaryForm.average.errorText"
                                      @focus="removeError(primaryForm.average)"
                                      @blur="blur(primaryForm.average)"
                                      :placeholder="'Средний чек, руб.'"></input-price>
                    </div>

                    <div class="row-b__col elem-pos-fit" :class="{'--active':primaryForm.average.focus}">
                        <div class="text-size-s construct-form__note" :class="{'text-color-red':primaryForm.average.error && primaryForm.average.value.length == 0}">
                            Среднестатистическая сумма покупки<br>
                            в вашей компании
                        </div>
                    </div>
                </div>


                <div class="row-b"  id="cooperation-discount">
                    <div class="row-b__col elem-pos-fix" style="width: 320px; ">
                        <input-styled  v-model="primaryForm.discount.value"
                                       :mask="masks.percent"
                                       :disabled="currentStep > 4"
                                       :isValidate="currentStep > 4"
                                       name="discount"
                                       :is-error="primaryForm.discount.error"
                                       :error-text="primaryForm.discount.errorText"
                                       @focus="removeError(primaryForm.discount)"
                                       @blur="blur(primaryForm.discount)"
                                       :placeholder="'Обычная скидка, %'"></input-styled>
                    </div>

                    <div class="row-b__col elem-pos-fit" :class="{'--active':primaryForm.discount.focus}">
                        <div class="text-size-s construct-form__note">
