                                      :disabled="currentStep > 4"
                                      :isValidate="currentStep > 4"
                                      name="projectDiscount"
                                      ref="projectDiscount"
                                      :is-error="primaryForm.projectDiscount.error"
                                      :error-text="primaryForm.projectDiscount.errorText"
                                      @focus="removeError(primaryForm.projectDiscount)"
                                      @blur="blur(primaryForm.projectDiscount)"
                                      :placeholder="'Эксклюзивная скидка, %'"></input-styled>
                    </div>

                    <div class="row-b__col elem-pos-fit" :class="{'--active':primaryForm.projectDiscount.focus}">
                        <div class="text-size-s construct-form__note">
                            <span class="text-color-red" v-if="primaryForm.projectDiscount.errorType === 1">Эксклюзивная сикдка для нашего проекта меньше той, <br>что вы предоставляете обычным покупателям. Так не пойдёт...</span>
                            <span class="text-color-red" v-if="primaryForm.projectDiscount.errorType === 2">Эксклюзивная скидка почти такая же, как вы даёте обычным покупателям.<br>Так не пойдёт, разница должна составлять не менее {{ options.minDiscount }}%.</span>
                            <span class="text-color-red" v-if="primaryForm.projectDiscount.errorType === 3">Эксклюзивная сикдка такая же, как вы даёте обычным покупателям.<br>Так не пойдёт, разница должна составлять не менее {{ options.minDiscount }}%.</span>
                            <span :class="{'text-color-red':primaryForm.projectDiscount.error && primaryForm.projectDiscount.value.length == 0}" v-if="primaryForm.projectDiscount.errorType === 0">Максимальная скидка, которую вы готовы предоставить<br> эксклюзивно для нашего проекта</span>
                        </div>
                    </div>
                </div>



                <div class="row-b"  id="cooperation-contactPerson">
