                                     :error-text="companyForm.contactPerson.errorText"
                                     v-model="companyForm.contactPerson.value"
                                     :fields="companyForm.contactPerson.fields"
                                     @focus="removeError(companyForm.contactPerson)"
                                     @blur="blur(companyForm.contactPerson)"
                                     :placeholder="'Контактное лицо'"></input-multi>
                    </div>
                </div>



                <div class="row-b"  id="cooperation-phone">
                    <div class="row-b__col elem-pos-fix w-tooltip --field-phone" style="width: 320px; ">
                        <input-styled
                                ref="phone"
                                name="phone"
                                :is-block-arrow-phone="true"
                                :mask="{mask:'+7(999)999-99-99',showMaskOnHover: false}"
                                :placeholder="'Номер телефона'"
                                :is-error="companyForm.phone.error"
                                v-model="companyForm.phone.value"
                                :error-text="companyForm.phone.errorType === 'use' ? 'Номер уже используется':companyForm.phone.errorType === 'mobile' ? 'Только мобильные номера':companyForm.phone.errorText"
