                            и не может быть использован для составления новой заявки. Укажите другой номер.
                        </div>
                        <div v-else-if="companyForm.phone.errorType === 'order' && companyForm.phone.error" class="text-size-s construct-form__note text-color-red">
                            На этот номер уже зарегистрирована заявка и её текущий статус «{{companyForm.phone.errorTypeStatus}}».<br>
                            Для составления новой заявки используйте другой номер телефона или напишите на skidki@zems.pro
                        </div>
                        <div v-else class="text-size-s construct-form__note" :class="{'text-color-red':companyForm.phone.error && companyForm.phone.value.length == 0}">
                            Указывайте мобильный, так как в случае одобрения вашей заявки<br>
                            этот номер будет основным логином и на него придёт СМС-пароль
                        </div>
                    </div>
                </div>


                <div class="row-b"  id="cooperation-email">
                    <div class="row-b__col elem-pos-fix" style="width: 320px; ">
                        <input-styled :is-error="companyForm.email.error"
                                      name="pochta"
                                      :error-text="companyForm.email.value.length === 0 ? companyForm.email.errorText : 'Некорректный E-mail'"
                                      v-model="companyForm.email.value"
                                      @focus="removeError(companyForm.email)"
                                      @blur="blur(companyForm.email)"
                                      :placeholder-input="'info@domain.com'"
                                      :placeholder="'E-mail'"></input-styled>
                    </div>

                    <div class="row-b__col elem-pos-fit" :class="{'--active':companyForm.email.focus}">
                        <div class="text-size-s construct-form__note" :class="{'text-color-red':companyForm.email.error && companyForm.email.value.length == 0}">
                            Обязательно указывайте почту, которую вы регулярно<br>
                            проверяете и оперативно на неё отвечаете
                        </div>
                    </div>
                </div>


                <div class="row-b"  id="cooperation-site">
                    <div class="row-b__col elem-pos-fix" style="width: 320px; ">
                        <input-styled :is-error="companyForm.site.error"
                                      name="site"
                                      v-model="companyForm.site.value"
                                      :error-text="companyForm.site.value.length === 0 ? companyForm.site.errorText : 'Некорректный адрес сайта'"
                                      @focus="removeError(companyForm.site)"
                                      @blur="blur(companyForm.site)"
                                      :placeholder-input="'www.domain.com'"
                                      :placeholder="'Сайт'"></input-styled>
                    </div>

                    <div class="row-b__col elem-pos-fit" :class="{'--active':companyForm.site.focus}">
                        <div class="text-size-s construct-form__note" :class="{'text-color-red':companyForm.site.error && companyForm.site.value.length == 0}">
