                            А если вдруг у вас вообще нет сайта — мы вряд ли сработаемся...
                        </div>
                    </div>
                </div>



                <div class="row-b mt32"  id="cooperation-note">
                    <div class="row-b__col elem-pos-fit active">
                        <span class="inp-s-label --permanent">О компании</span>
                        <textarea-field :is-error="companyForm.note.error"
                                        v-model="companyForm.note.value"
                                        :error-text="companyForm.note.errorText"
                                        @focus="removeError(companyForm.note)"
                                        @blur="blur(companyForm.note)"
                                        :class="'full'" placeholder="Например, «Наша компания работает с 2009 года и является лучшей в городе. А еще мы готовы предоставить всем участникам проекта вип-сопровождение заказа и бесплатную доставку непосредственно до двери»"
                         :add-style="{height: 88 + 'px'}"></textarea-field>
                    </div>
                </div>


                <div class="mt32">
                    <div class="mt16"  id="cooperation-checkWhat">
                        <input @change="removeError(companyForm.checkWhat)" v-model="companyForm.checkWhat.value" :class="{error:companyForm.checkWhat.error}" type="checkbox" id="cooperation-info-what">
                        <label for="cooperation-info-what">Я знаю, <a @click="openCooperationWhat" href="javascript:void(0)">что даёт сотрудничество</a> и <a @click="openCooperationHow" href="javascript:void(0)">как работает проект</a></label>
                    </div>
                    <!--
                    <div class="mt16"  id="cooperation-checkHow">
                        <input @change="removeError(companyForm.checkHow)" v-model="companyForm.checkHow.value" :class="{error:companyForm.checkHow.error}" type="checkbox" id="cooperation-info-how">
                        <label for="cooperation-info-how">Я в курсе, <a @click="openCooperationHow" href="javascript:void(0)">как работает проект</a></label>
                    </div>
                  -->
                    <div class="mt16"  id="cooperation-checkTerms">
                        <input @change="removeError(companyForm.checkTerms)" v-model="companyForm.checkTerms.value" :class="{error:companyForm.checkTerms.error}" type="checkbox" id="cooperation-info-terms">
                        <label for="cooperation-info-terms">Я соответствую всем <a @click="openCooperationTerms" href="javascript:void(0)">условиям сотрудничества</a></label>
                    </div>

                    <div class="mt16"  id="cooperation-personal">
                        <input type="checkbox" id="cooperation-info-personal">
                        <label for="cooperation-info-personal">Я даю согласию на <a @click="openCooperationPersonalData" href="javascript:void(0)">обработку персональных данных</a></label>
                    </div>
                </div>


                <div class="row-b mt32">
                    <submit-btn tag="button"
                                class="btn"
                                @click.native="sendForm"
                                :type="buttonSave">
                        Отправить на рассмотрение
                    </submit-btn>
                </div>
            </div>
            <!-- end cooperation__form -->


            <popup :is-close-out-size="false" :is-btn-close="false" :show="popups.cooperationAccepted.show" :name="popups.cooperationAccepted.name">
                <cooperation-accepted></cooperation-accepted>
            </popup>


            <popup :is-btn-close="false" :show="popups.cooperationErrorPhone.show" :name="popups.cooperationErrorPhone.name">
                <cooperation-error-phone
                        :phone="companyForm.phone.value"
                        @close="setFocus('phone')"></cooperation-error-phone>
            </popup>


            <popup :is-btn-close="false" :show="popups.cooperationCancelSmallPrice.show" :name="popups.cooperationCancelSmallPrice.name" :width="992" :add-class="'popup-cooperation-price'">
                <cooperation-cancel-small-price @close="setFocus('projectDiscount')" :form="primaryForm"></cooperation-cancel-small-price>
            </popup>


            <popup :is-btn-close="false" :show="popups.cooperationCancelVerySmallPrice.show" :name="popups.cooperationCancelVerySmallPrice.name" :width="992" :add-class="'popup-cooperation-price'">
                <cooperation-cancel-very-small-price @close="setFocus('projectDiscount')" :form="primaryForm"></cooperation-cancel-very-small-price>
            </popup>

            <popup :width="720" :is-btn-close="false" :show="popups.cooperationCancelSmallPercent.show" :name="popups.cooperationCancelSmallPercent.name">
                <div>
                    <div class="popup__header mb">
                        <div class="title">Предложенная скидка слишком мала</div>
                    </div>


                    <div class="popup__content">
                        <b>Какой должна быть минимальная скидка?</b>
                        <ul class="ul-disc-small mt0">
                            <li>Общая скидка должна составлять не менее {{ options.minDiscount }}%, потому что {{ options.ourDiscount }}% из этой скидки – наша комиссия.</li>
                            <li>Оставшиеся проценты будут составлять скидку для покупателя. Поэтому {{ options.minDiscount }}% – это самый минимум.</li>
                        </ul>

                        <div class="row-b mt32">
                            <button class="btn btn--white-red" @click="closeCooperationCancelSmallPercent()">Предложить более солидную скидку</button>
                        </div>
                    </div>
                </div>
            </popup>

            <popup :is-btn-close="false" :show="popups.cooperationCancelSmallPriceOne.show" :name="popups.cooperationCancelSmallPriceOne.name" :width="808">
                <cooperation-cancel-small-price-one
                        @close="setFocus('projectDiscount')"
                        :average="primaryForm.average.value"
                        :discount="primaryForm.discount.value"
                        :projectDiscount="primaryForm.projectDiscount.value"
                ></cooperation-cancel-small-price-one>
            </popup>

            <!-- что дает сотрудничество -->
            <popup :width="736" :is-set-blank="true" :show="popups.cooperationWhat.show" :name="popups.cooperationWhat.name" :add-class="'popup--cooperation'">
                <cooperation-what></cooperation-what>
            </popup>

            <!-- Как это работает? -->
            <popup :width="736" :is-set-blank="true" :show="popups.cooperationHow.show" :name="popups.cooperationHow.name" :add-class="'popup--cooperation'">
                <cooperation-how></cooperation-how>
            </popup>

            <!-- Условия сотрудничества -->
            <popup :width="736" :is-set-blank="true" :show="popups.cooperationTerms.show" :name="popups.cooperationTerms.name" :add-class="'popup--cooperation'">
                <cooperation-terms></cooperation-terms>
            </popup>

            <popup :width="736" :is-set-blank="true" :show="popups.cooperationPersonalData.show" :name="popups.cooperationPersonalData.name" :add-class="'popup--cooperation'">
                <cooperation-personal-data></cooperation-personal-data>
            </popup>

        </div>
    </div>
