<script>
    import { mapState } from 'vuex'
    //import {FormWizard, TabContent} from '@/helpers/vue-form-wizard';
    import InputStyled from '@/components/forms/InputStyled.vue';
    import InputMulti from '@/components/forms/InputMulti.vue';
    import InputPrice from '@/components/forms/InputPrice.vue';
    import TextareaField from '@/components/forms/TextareaField.vue';
    import Popup from '@/components/modal/Popup.vue';
    import CooperationAccepted from '@/components/modal/CooperationAccepted.vue';
    import CooperationErrorPhone from '@/components/modal/CooperationErrorPhone.vue';
    import CooperationCancelSmallPrice from '@/components/modal/CooperationCancelSmallPrice.vue';
    import CooperationCancelSmallPriceOne from '@/components/modal/CooperationCancelSmallPriceOne.vue';
    import CooperationCancelVerySmallPrice from '@/components/modal/CooperationCancelVerySmallPrice.vue';
    import CitySelect from '@/components/CitySelect.vue'
    import {scrollTo} from '@/helpers/animate.js';
    import CooperationWhat from '@/components/modal/CooperationWhat.vue';
    import CooperationHow from '@/components/modal/CooperationHow.vue';
    import CooperationTerms from '@/components/modal/CooperationTerms.vue';
    import CooperationPersonalData from '@/components/modal/CooperationPersonalData.vue';
    import SubmitBtn from '@/components/forms/SubmitBtn.vue';

    export default {
        metaInfo() {
            return {
                title: 'Сотрудничество',
                titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        name: "Cooperation",
        components: {
            //FormWizard,
            //TabContent,
            InputStyled,
            InputPrice,
            InputMulti,
            TextareaField,
            Popup,
            CitySelect,
            CooperationAccepted,
            CooperationErrorPhone,
            CooperationCancelSmallPrice,
            CooperationCancelVerySmallPrice,
            CooperationCancelSmallPriceOne,
            CooperationWhat,
            CooperationHow,
            CooperationTerms,
            CooperationPersonalData,
            SubmitBtn,
        },
        data: function () {
            return {
                steps: 4, //кол-во шагов
                currentStep: 1,
                buttonSave: 'normal',
                cityFocus: false,
                primaryForm: {
                    direction: {
                        name: 'direction',
                        value: '',
                        required: true,
                        error: false,
                        errorText: 'Обязательное поле',
                        disabled: false,
                        focus:false,
                        maxLength: 50,
                        sortScroll: 3
                    },
                    city: {
                        name: 'city',
                        value: '',
                        valueText: '',
                        error: false,
                        errorText: 'Обязательное поле',
                        disabled: false,
                        focus:false,
                        sortScroll: 2
                    },
                    average: {
                        name: 'average',
                        value: '',
                        required: true,
                        error: false,
                        errorText: 'Обязательное поле',
                        disabled: false,
                        focus:false,
                        sortScroll: 4
                    },
                    projectDiscount: {
                        name: 'projectDiscount',
                        value: '',
                        required: true,
                        error: false,
                        minValue: 1,
                        errorText: 'Обязательное поле',
                        disabled: false,
                        focus:false,
                        sortScroll: 6,
                        errorType: 0,
                    },
                    discount: {
                        name: 'discount',
                        value: '',
                        required: false,
                        error: false,
                        errorText: 'Обязательное поле',
                        disabled: false,
                        focus:false,
                        sortScroll: 5
                    },
                },
                companyForm: {
                    contactPerson: {
                        name: 'contactPerson',
                        value: '',
                        fields: {
                            lastName: {
                                name: 'lastName',
                                value: '',
                                delimiter: ' ',
                                placeholder: 'Фамилия',
                            },
                            firstName: {
                                name: 'firstName',
                                value: '',
                                delimiter: ' ',
                                placeholder: 'Имя',
                            },
                            secondName: {
                                name: 'secondName',
                                value: '',
                                delimiter: '',
                                placeholder: 'Отчество',
                            },
                            position: {
                                name: 'position',
                                value: '',
                                postfix: ',',
                                delimiter: '',
                                placeholder: ', должность',
                            },
                        },
                        required: true,
                        error: false,
                        errorText: 'Обязательное поле',
                        disabled: false,
                        focus:false,
                        sortScroll: 7
                    },
                    company: {
                        name: 'company',
                        value: '',
                        required: true,
                        error: false,
                        errorText: 'Обязательное поле',
                        disabled: false,
                        focus:false,
                        sortScroll: 1
                    },
                    phone: {
                        name: 'phone',
                        value: '',
                        required: true,
                        error: false,
                        errorType: 'empty',
                        errorText: 'Обязательное поле',
                        disabled: false,
                        focus:false,
                        errorTypeStatus: '',
                        sortScroll: 8
                    },
                    email: {
                        name: 'email',
                        value: '',
                        required: true,
                        error: false,
                        errorText: 'Обязательное поле',
                        disabled: false,
                        focus:false,
                        sortScroll: 9
                    },
                    site: {
                        name: 'site',
                        value: '',
                        required: true,
                        error: false,
                        errorText: 'Обязательное поле',
                        disabled: false,
                        focus:false,
                        sortScroll: 10
                    },
                    note: {
                        name: 'note',
                        value: '',
                        required: true,
                        error: false,
                        errorText: 'Обязательное поле',
                        disabled: false,
                        focus:false,
                        sortScroll: 11
                    },
                    checkWhat:{
                        name: 'checkWhat',
                        value: '',
                        required: true,
                        error: false,
                        errorText: 'Обязательное поле',
                        disabled: false,
                        focus:false,
                        sortScroll: 12
                    },
                    checkHow:{
                        name: 'checkHow',
                        value: '',
                        required: true,
                        error: false,
                        errorText: 'Обязательное поле',
                        disabled: false,
                        focus:false,
                        sortScroll: 13
                    },
                    checkTerms:{
                        name: 'checkTerms',
                        value: '',
                        required: true,
                        error: false,
                        errorText: 'Обязательное поле',
                        disabled: false,
                        focus:false,
                        sortScroll: 14
                    },
                }
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                options: state => state.options.items,
                masks: state => state.masks.items,
                currentCityIp:  state => state.cities.currentCityIp,
            }),
        },
        mounted: function(){
            this.primaryForm.city.value = this.currentCityIp;
            ym(39511350, 'hit', this.$route.path, {title: 'Сотрудничество | ZemSkidki.ru'});
        },
        methods: {
            setFocus: function(ref) {
                if(!!this.$refs[ref]){
                    let input = this.$refs[ref].$el.getElementsByClassName('inp-s')[0];
                    if(input){

                        input.focus()
                        setTimeout(function () {
                            input.select()
                        },20)
                    }
                }
            },
            sendForm: function () {
                let validPrimary = this.validPrimary();
                if(validPrimary){
                    let fields = {};
                    for(let fieldIndex in this.companyForm) {
                        let field = this.companyForm[fieldIndex];
                        if(field.name ==='contactPerson'){
                            for(let fieldIndexPerson in field.fields) {
                                let fieldPerson = field.fields[fieldIndexPerson];
                                fields[fieldPerson.name] = fieldPerson.value.replace(',','').trim()
                            }
                        } else {
                            fields[field.name] = field.value.toString().trim();
                        }
                    }
                    for(let fieldIndex in this.primaryForm) {
                        let field = this.primaryForm[fieldIndex];
                        if(field.name === 'city') {
                            if(field.value.xmlId){
                                fields[field.name] = field.value.xmlId;
                            } else {
                                fields[field.name] = this.currentCityIp.xmlId
                            }
                        }
                        else if(field.name === 'average'){
                            fields[field.name] = field.value.split(' ').join('');
                        } else {
                            fields[field.name] = field.value.toString().trim();
                        }

                    }
                    fields.formName = 'cooperation';
                    this.buttonSave = 'load';
                    this.$store.dispatch('forms/sendForm', {params: fields}).then(()=>{
                        this.buttonSave = 'ok';
                        setTimeout(() => {
                            this.buttonSave = 'normal';
                        }, 300);
                    });
                }
            },
            setPersonInput: function (field) {
                if(field.value !== undefined && field.name !== undefined){
                    this.companyForm.contactPerson.fields[field.name].value = field.value;
                }
            },
            stepChange: function() {
                window.location.hash = 'step' + this.$refs.wizard.activeTabIndex; //
            },
            stepFinal: function() {
            },
            startStep: function() {
                let stepHash;

                if(window.location.hash !== '') {
                    stepHash = parseInt(window.location.hash.replace('#step',''), 10);
                    if(stepHash > 3) {
                        stepHash = 3;
                        window.location.hash = 'step' + stepHash;
                    }
                } else {
                    stepHash = 0;
                }

                return stepHash;
            },
            /*prevStep: function() {
                this.currentStep--;
                this.$nextTick(function () {
                    scrollTo('#cooperation-step-'+this.currentStep,true,-50);
                })
            },*/
            setCity: function(city) {
                this.primaryForm.city.value = city;
                this.primaryForm.city.valueText = city.name;
                this.cityFocus = false;
            },
            validPrimary: function() {
                let error = false;
                let scrollToField = {
                    sortScroll: 99
                };
                for(let fieldIndex in this.primaryForm) {
                    let field = this.primaryForm[fieldIndex];
                    let errorField = false;
                    if(field.required) {
                        if(field.maxLength && field.value.length > field.maxLength) {
                            error = errorField = true;
                        }
                        if(field.minValue && +field.value < field.minValue) {
                            error = errorField = true;
                        }
                        if(field.value.length === 0){
                            error = errorField = true;
                        }
                        if(field.name === 'projectDiscount' && field.value > 0) {
                            if(+field.value < +this.primaryForm.discount.value){
                                error = errorField = true;
                                field.errorType = 1;
                            } else if(+field.value == +this.primaryForm.discount.value){
                                error = errorField = true;
                                field.errorType = 3;
                            } else if(field.value - this.primaryForm.discount.value < +this.options.minDiscount){
                                error = errorField = true;
                                field.errorType = 2;
                            }
                        }

                        if(errorField && scrollToField.sortScroll > field.sortScroll) {
                            scrollToField = field;
                        }
                    }
                    field.error = false;
                    setTimeout(()=>{
                        field.error = errorField
                    },150);

                }

                let rEmail = /^[\w\.\d-_]+@[\w\.\d-_]+\.\w{2,99}$/i;
                let rSite = /^(http(s)?:\/\/)?([\w-А-Яа-я]+\.)+[\w-А-Яа-я]+(\/[\w-А-Яа-я ;,./?%&=]*)?$/;
                for(let fieldIndex in this.companyForm) {
                    let field = this.companyForm[fieldIndex];
                    let errorField = false;
                    if(field.required) {
                        if(field.name ==='contactPerson') {
                            for(let fieldIndexPerson in field.fields) {
                                let fieldPerson = field.fields[fieldIndexPerson];
                                if(fieldPerson.value.length === 0){
                                    error = errorField = true;
                                }
                            }
                        } else {
                            if(field.maxLength && field.value.length > field.maxLength) {
                                error = errorField = true;
                            }
                            if(field.value.length === 0){
                                error = errorField = true;
                            }
                            if(field.value === false){
                                error = errorField = true;
                            }
                            if(field.name === 'email' && !rEmail.test(field.value)) {
                                error = errorField = true;
                            }
                            if(field.name === 'site' && !rSite.test(field.value)) {
                                error = errorField = true;
                            }

                            if(field.name === 'phone') {
                                if(field.errorType === 'private' || field.errorType === 'order'){
                                    error = errorField = true;
                                } else {
                                    let phone = field.value.replace(/[^\+0-9]/g, '');
                                    if (phone.length < 12) {
                                        error = errorField = true;
                                        if(phone.length > 0){
                                            field.errorType = 'mobile';
                                        }
                                    } else if (phone[2] != '9') {
                                        error = errorField = true;
                                        field.errorType = 'mobile';
                                    }
                                }

                            }
                        }
                    }
                    field.error = false;
                    setTimeout(()=>{
                        field.error = errorField
                    },150);


                    if(errorField && scrollToField.sortScroll > field.sortScroll) {
                        scrollToField = field;
                    }
                }
                if(error && scrollToField) {
                    scrollTo('#cooperation-'+scrollToField.name,true,-50);
                }
                if(!error){
                    let discount = this.primaryForm.projectDiscount.value - this.primaryForm.discount.value - 3;
                    if (+this.primaryForm.average.value.split(' ').join('') > 0 && +this.primaryForm.projectDiscount.value > 0) {
                        if ((+this.primaryForm.average.value.split(' ').join('') * (discount) / 100) < this.options.cooperationProfit) {
                            this.openCooperationCancelSmallPriceOne();
                            error = true;
                        }
                    }
                }
                return !error;
            },
            checkPhone(field){
                field.focus = false;
                let phone = field.value.replace(/[^\+0-9]/g, '');
                let error = false;
                if (phone.length < 12) {
                    error = true;
                } else if (phone[2] != '9') {
                    error = true;
                }
                if(!error){
                    this.$store.dispatch('users/checkPhone', {phone: field.value}).then((e)=>{
                        if(e && e.code){
                            field.error = true;
                            field.errorType = e.code;
                            field.errorTypeStatus = e.statusOrder;
                        } else {
                            field.errorType = 'mobile';
                            field.errorTypeStatus = ''
                        }
                    });
                } else {
                    if(field.value.length > 0) {
                        field.errorType = 'mobile';
                        //field.error = true
                    } else {
                        field.errorType = 'empty';
                        //field.error = true
                    }
                }


            },
            removeError: function(field) {
                field.error = false;
                field.focus = true;
                if(field.name === 'projectDiscount'){
                    field.errorType = 0;
                }
            },
            blur(field){
                field.focus = false
                this.validFiled(field);
            },
            validFiled(field){
                if(field.value.length > 0){
                    let error = false;
                    let rEmail = /^[\w\.\d-_]+@[\w\.\d-_]+\.\w{2,99}$/i;
                    let rSite = /^(http(s)?:\/\/)?([\w-А-Яа-я]+\.)+[\w-А-Яа-я]+(\/[\w-А-Яа-я ;,./?%&=]*)?$/;
                    if(field.name === 'email' && !rEmail.test(field.value)) {
                        error = true;
                    }
                    if(field.name === 'site' && !rSite.test(field.value)) {
                        error = true;
                    }
                    field.error = error
                }

            },
            nextStep: function(){
                switch (this.currentStep) {
                    case 1:
                        this.currentStep = 2;
                        this.$nextTick(function () {
                            scrollTo('#cooperation-step-'+this.currentStep,true,-50);
                        })
                        break;
                    case 2:
                        this.currentStep = 3;
                        this.$nextTick(function () {
                            scrollTo('#cooperation-step-'+this.currentStep,true,-50);
                        })
                        break;
                    case 3:
                        this.currentStep = 4;
                        this.$nextTick(function () {
                            scrollTo('#cooperation-step-'+this.currentStep,true,-50);
                        })

                        break;
                    case 4:
                        if(this.validPrimary()) {
                            this.currentStep = 5;
                            this.$nextTick(function () {
                                scrollTo('#cooperation-step-'+this.currentStep,true,-50);
                            })
                        }
                        break;
                    default:
                        this.$refs.wizard.nextTab();
                        this.stepChange()
                        break;
                }
            },
            openCitySelect() {
                this.$store.dispatch('popups/open', {name: this.popups.citySelectReturn.name});
            },
            openCooperationAccepted: function () {
                this.$store.dispatch('popups/open', {name: this.popups.cooperationAccepted.name});
            },
            openCooperationErrorPhone: function () {
                this.$store.dispatch('popups/open', {name: this.popups.cooperationErrorPhone.name});
            },
            openCooperationCancelSmallPriceOne: function () {
                this.$store.dispatch('popups/open', {name: this.popups.cooperationCancelSmallPriceOne.name});
            },
            openCooperationCancelSmallPrice: function () {
                this.$store.dispatch('popups/open', {name: this.popups.cooperationCancelSmallPrice.name});
            },
            openCooperationCancelVerySmallPrice: function () {
                this.$store.dispatch('popups/open', {name: this.popups.cooperationCancelVerySmallPrice.name});
            },
            openCooperationWhat: function () {
                this.$store.dispatch('popups/open', {name: this.popups.cooperationWhat.name});
            },
            openCooperationHow: function () {
                this.$store.dispatch('popups/open', {name: this.popups.cooperationHow.name});
            },
            openCooperationTerms: function () {
                this.$store.dispatch('popups/open', {name: this.popups.cooperationTerms.name});
            },
            openCooperationCancelSmallPercent: function () {
                this.$store.dispatch('popups/open', {name: this.popups.cooperationCancelSmallPercent.name});
            },
            openCooperationPersonalData: function () {
                this.$store.dispatch('popups/open', {name: this.popups.cooperationPersonalData.name});
            },
            closeCooperationCancelSmallPercent: function () {
                this.$store.commit('popups/close', {name: this.popups.cooperationCancelSmallPercent.name});
                this.setFocus('projectDiscount');
            },

        },
        directives: {
            'clickoutside': {
                bind: function(el, binding, vNode) {
                    if (typeof binding.value !== 'function') {
                        const compName = vNode.context.name;
                        let warn = `[Vue-click-outside:] provided expression '${binding.expression}' is not a function, but has to be`
                        if (compName) { warn += `Found in component '${compName}'` }
                        //console.warn(warn);
                    }
                    const bubble = binding.modifiers.bubble;
                    const handler = (e) => {
                        if (bubble || (!el.contains(e.target) && el !== e.target) && (!e.target.classList.contains('click-outside-tooltip'))) {
                            binding.value(e)
                        }
                    }
                    el.__vueClickOutside__ = handler;
                    document.addEventListener('click', handler);
                },

                unbind: function(el, binding) {
                    document.removeEventListener('click', el.__vueClickOutside__);
                    el.__vueClickOutside__ = null;
                }
            }
        }
    }
</script>

<style>
    .inp-s-label.--permanent{
        font-weight: normal;
        font-size: 10px;
        position: relative;
        bottom: 8px;
    }
</style>
