<template>
    <div class="popup-info">
        <div>
            <img src="@/assets/img/icon-warning-penis-2.svg" alt="">
        </div>

        <div class="text mt32">
            <div class="text-size-xxl text-color-red text-bolder">
                К сожалению,<br>
                вынуждены отказать в сотрудничестве!
            </div>

            <ul class="ul-disc-small mt24 text-left">
                <li>Средний чек в вашей компании составляет {{ number_format(form.average.value) }}<span class="rouble">{{ $store.getters['options/setRouble'] }}</span>, а максимальная скидка для розничного покупателя составляет <span v-html="form.discount.value"></span>%.</li>
                <li>Для нас вы готовы сделать скидку <span v-html="form.projectDiscount.value"></span>% (скидка покупателя составит <span v-html="form.projectDiscount.value - 3 > 0 ? form.projectDiscount.value - 3 : 0"></span>% и еще 3% — доход нашего проекта).</li>
                <li>Получается, что самостоятельно покупатель может получить скидку {{ discountSumRetail }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>, а благодаря нашему проекту — примерно {{ discountSum }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>.</li>
                <li><strong>В результате выгода покупателя составит всего {{ discountSumUser }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>, что очень мало по сравнению с покупкой в {{ number_format(form.average.value) }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>. Спасибо за понимание!</strong></li>
            </ul>
        </div>

        <div class="mt32">
            <button class="btn btn--white-green" style="min-width: 328px; " @click="close()">Предложить более солидную скидку</button>
            <router-link @click.native="close" style="min-width: 328px; " tag="button" to="/" class="btn btn--white-red  ml24" >Отказаться от участия в проекте</router-link>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import Popup from '@/components/modal/Popup.vue'
    import {number_format} from '@/helpers/formatted';

    export default {
        name: "cooperationCancelSmallPrice",
        components: {
            Popup
        },
        props: {
            form: {
                default: function () {
                    return {}
                }
            }
        },
        methods: {
            close: function () {
                this.$emit('close');
                this.$store.commit('popups/close', {name: this.popups.cooperationCancelSmallPrice.name});
            },
            number_format: function (v) {
                return number_format(v, 0, '', ' ')
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
            discountSumRetail: function () {
                let discountSum = this.form.average.value * this.form.discount.value / 100;
                return number_format(discountSum > 0 ? discountSum : 0, 0, '', ' ')
            },
            discountSumUser: function () {
                let discountSum = this.form.average.value * (this.form.projectDiscount.value - this.form.discount.value - 3) / 100;
                return number_format(discountSum > 0 ? discountSum : 0, 0, '', ' ')
            },
            discountSum: function () {
                let discountSum = this.form.average.value * (this.form.projectDiscount.value - 3) / 100;
                return number_format(discountSum > 0 ? discountSum : 0, 0, '', ' ')
            },
        }
    }
</script>

