<template>
    <div>
        <div class="popup__header mb">
            <div class="title" v-if="$route.name==='cooperation'">Польза от вашего предложения слишком мала</div>
            <div class="title" v-else>Польза от вашей акции слишком мала</div>
        </div>


        <div class="popup__content mt24">
            <p class="text-size-ml">Разница между обычной и эксклюзивной скидкой составляет всего 
                <strong>{{ discountSumUser }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong>. 
                <br>
                Это несерьёзно, должно быть <b>минимум {{options.cooperationProfit}} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>.</b></p>
                
            <p class="text-size-ml mt24">Как мы это посчитали?</p>
            
            <ul class="ul-disc-small mt16">
                <li>Средний чек в вашей компании составляет {{number_format(average)}} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></li>
                <li>Максимальная скидка для розничного покупателя составляет {{ discountSumRetail }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span> <span v-if="discount.toString().length > 0">(<span v-html="discount"></span>%)</span></li>
                <li>Для нас вы готовы сделать эксклюзивную скидку <span v-html="projectDiscount"></span>% (из которых {{ options.ourDiscount }}% – это комиссия проекта)</li>
                <li>В результате покупатель получит скидку {{ discountSum }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span> (<span v-html="projectDiscount - options.ourDiscount"></span>%)</li>
                <li>Разница между обычной и эксклюзивной скидкой составит <b>всего {{ discountSumUser }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></b></li>
                <li>А нам бы хотелось, чтобы польза от нашего проекта составляла <b>не менее {{options.cooperationProfit}} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></b></li>
            </ul>
        </div>
        
        <div class="mt32">
            <button class="btn btn--white-red" style="min-width: 328px; " @click="close()">Предложить более солидную скидку</button>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import Popup from '@/components/modal/Popup.vue'
    import {number_format} from '@/helpers/formatted';

    export default {
        name: "CooperationCancelSmallPriceOne",
        components: {
            Popup
        },
        props: {
            average: {
                default: function () {
                    return {}
                }
            },
            discount: {
                default: function () {
                    return {}
                }
            },
            projectDiscount: {
                default: function () {
                    return {}
                }
            },

        },
        methods: {
            close: function () {
                this.$emit('close');
                this.$store.commit('popups/close', {name: this.popups.cooperationCancelSmallPriceOne.name});
            },
            number_format: function (v) {
                return number_format(v, 0, '', ' ')
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                options: state => state.options.items,
            }),
            discountSumRetail: function () {
                let discountSum = this.average * this.discount / 100;
                return number_format(discountSum > 0 ? discountSum : 0, 0, '', ' ')
            },
            discountSumUser: function () {
                let discountSum = this.average * (this.projectDiscount - this.discount - this.options.ourDiscount) / 100;
                return number_format(discountSum > 0 ? discountSum : 0, 0, '', ' ')
            },
            discountSum: function () {
                let discountSum = this.average * (this.projectDiscount - this.options.ourDiscount) / 100;
                return number_format(discountSum > 0 ? discountSum : 0, 0, '', ' ')
            },
        }
    }
</script>

<style scoped>
    .ul-disc-small li {
        margin-top: 4px !important;
    }
</style>